// ==============================================
// RISIVO CRM - Email Accounts API
// GET: List email accounts, POST: Create email account
// ==============================================

import { NextRequest, NextResponse } from 'next/server'
import { prisma } from '@/lib/db'
import { createResellerClubClient } from '@/lib/email-infrastructure/resellerclub'
import * as crypto from 'crypto'

// Hash password for storage
function hashPassword(password: string): string {
  return crypto.createHash('sha256').update(password).digest('hex')
}

// GET /api/email-infrastructure/email-accounts
// List all email accounts for a sub-account or domain
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const domainId = searchParams.get('domainId')
    const status = searchParams.get('status')
    const warmupEnabled = searchParams.get('warmupEnabled')
    const search = searchParams.get('search')

    // Build query filters
    const where: any = {}

    if (subAccountId) {
      where.subAccountId = subAccountId
    }

    if (domainId) {
      where.domainId = domainId
    }

    if (status && status !== 'all') {
      where.status = status
    }

    if (warmupEnabled !== null && warmupEnabled !== undefined) {
      where.warmupEnabled = warmupEnabled === 'true'
    }

    if (search) {
      where.OR = [
        { email: { contains: search, mode: 'insensitive' } },
        { displayName: { contains: search, mode: 'insensitive' } },
      ]
    }

    const emailAccounts = await prisma.emailAccount.findMany({
      where,
      include: {
        domain: {
          select: {
            id: true,
            domainName: true,
            status: true,
            spfConfigured: true,
            dkimConfigured: true,
            dmarcConfigured: true,
          },
        },
      },
      orderBy: { createdAt: 'desc' },
    })

    // Calculate stats
    const stats = {
      total: emailAccounts.length,
      active: emailAccounts.filter(e => e.status === 'active').length,
      warmupEnabled: emailAccounts.filter(e => e.warmupEnabled).length,
      campaignEnabled: emailAccounts.filter(e => e.campaignEnabled).length,
      avgReputationScore: emailAccounts.length > 0
        ? Math.round(
            emailAccounts.reduce((sum, e) => {
              // Calculate a mock reputation score based on warmup status
              let score = 50
              if (e.warmupStatus === 'ready') score = 85
              else if (e.warmupStatus === 'warming') score = 60
              return sum + score
            }, 0) / emailAccounts.length
          )
        : 0,
    }

    return NextResponse.json({
      success: true,
      data: emailAccounts,
      stats,
    })
  } catch (error) {
    console.error('[API] List email accounts error:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to fetch email accounts' },
      { status: 500 }
    )
  }
}

// POST /api/email-infrastructure/email-accounts
// Create a new email account
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const {
      subAccountId,
      domainId,
      localPart, // username part (before @)
      displayName,
      password,
      storageQuota = 5120, // 5GB default
      sendLimitDaily = 500,
      enableWarmup = true,
      enableCampaigns = true,
      forwardingAddress,
    } = body

    if (!subAccountId || !domainId || !localPart || !password) {
      return NextResponse.json(
        { success: false, error: 'subAccountId, domainId, localPart, and password are required' },
        { status: 400 }
      )
    }

    // Validate localPart
    const localPartRegex = /^[a-z0-9._-]+$/
    if (!localPartRegex.test(localPart.toLowerCase())) {
      return NextResponse.json(
        { success: false, error: 'Invalid email username. Use only letters, numbers, dots, underscores, and hyphens.' },
        { status: 400 }
      )
    }

    // Get domain
    const domain = await prisma.domain.findUnique({
      where: { id: domainId },
      include: {
        emailAccounts: true,
      },
    })

    if (!domain) {
      return NextResponse.json(
        { success: false, error: 'Domain not found' },
        { status: 404 }
      )
    }

    if (domain.status !== 'active') {
      return NextResponse.json(
        { success: false, error: 'Domain is not active' },
        { status: 400 }
      )
    }

    if (!domain.emailEnabled) {
      return NextResponse.json(
        { success: false, error: 'Email is not enabled for this domain' },
        { status: 400 }
      )
    }

    // Check email account limit
    if (domain.emailAccounts.length >= domain.maxEmailAccounts) {
      return NextResponse.json(
        { 
          success: false, 
          error: `Maximum email accounts (${domain.maxEmailAccounts}) reached for this domain`,
        },
        { status: 400 }
      )
    }

    // Check if email already exists
    const fullEmail = `${localPart.toLowerCase()}@${domain.domainName}`
    const existingEmail = await prisma.emailAccount.findUnique({
      where: { email: fullEmail },
    })

    if (existingEmail) {
      return NextResponse.json(
        { success: false, error: 'Email address already exists' },
        { status: 400 }
      )
    }

    // Initialize ResellerClub client
    const resellerClub = createResellerClubClient()
    let resellerClubEmailId: string | null = null

    // If ResellerClub is configured, create email through API
    if (resellerClub) {
      try {
        const result = await resellerClub.createEmailAccount(domain.domainName, {
          email: fullEmail,
          password,
          firstName: displayName?.split(' ')[0] || localPart,
          lastName: displayName?.split(' ').slice(1).join(' ') || '',
          storageQuota,
        })

        if (result) {
          resellerClubEmailId = result.emailId
        }
      } catch (apiError) {
        console.error('[API] ResellerClub email creation failed:', apiError)
        // Continue with local-only creation for demo purposes
      }
    }

    // Create email account
    const emailAccount = await prisma.emailAccount.create({
      data: {
        subAccountId,
        domainId,
        email: fullEmail,
        localPart: localPart.toLowerCase(),
        displayName,
        status: resellerClubEmailId ? 'active' : 'pending',
        resellerClubEmailId,
        storageQuota,
        storageUsed: 0,
        sendLimitDaily,
        sentToday: 0,
        passwordHash: hashPassword(password),
        lastPasswordChange: new Date(),
        smtpEnabled: true,
        imapEnabled: true,
        smtpHost: `smtp.${domain.domainName}`,
        smtpPort: 587,
        imapHost: `imap.${domain.domainName}`,
        imapPort: 993,
        forwardingEnabled: !!forwardingAddress,
        forwardingAddress,
        warmupEnabled: enableWarmup,
        warmupStatus: enableWarmup ? 'pending' : null,
        campaignEnabled: enableCampaigns,
      },
      include: {
        domain: {
          select: {
            id: true,
            domainName: true,
            status: true,
          },
        },
      },
    })

    // If warmup is enabled, create a warmup mailbox entry
    if (enableWarmup) {
      try {
        await prisma.warmupMailbox.create({
          data: {
            subAccountId,
            email: fullEmail,
            displayName: displayName || localPart,
            provider: 'custom',
            connectionType: 'smtp',
            smtpHost: `smtp.${domain.domainName}`,
            smtpPort: 587,
            smtpUsername: fullEmail,
            imapHost: `imap.${domain.domainName}`,
            imapPort: 993,
            imapUsername: fullEmail,
            status: 'pending',
            warmupMode: 'balanced',
            dailyLimit: 5, // Start slow
            warmupDays: 0,
            targetDailyLimit: 50,
            reputationScore: 50,
          },
        })

        // Link the warmup mailbox to the email account
        const warmupMailbox = await prisma.warmupMailbox.findFirst({
          where: { email: fullEmail },
        })

        if (warmupMailbox) {
          await prisma.emailAccount.update({
            where: { id: emailAccount.id },
            data: { warmupMailboxId: warmupMailbox.id },
          })
        }
      } catch (warmupError) {
        console.error('[API] Failed to create warmup mailbox:', warmupError)
        // Non-critical, continue
      }
    }

    return NextResponse.json({
      success: true,
      data: emailAccount,
      message: 'Email account created successfully',
      warmupEnabled: enableWarmup,
    })
  } catch (error) {
    console.error('[API] Create email account error:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to create email account' },
      { status: 500 }
    )
  }
}
